export default class AddProjectPage
{
    constructor(project_header, project_main, container_2)
    {
        this.project_header = project_header 
        this.project_main = project_main 
        this.container_2 = container_2 
        
        this.addContainer()
        this.addHeader()
        this.addMain()
    }

    addHeader()
    {
        this.project_header.style.transform="translateY(0%)"
        this.project_header.style.transitionProperty="transform"
    }
    addMain()
    {
        this.project_main.style.transform="translateY(0%)"
        this.project_main.style.transitionProperty="transform"
    }
    addContainer()
    {
        this.container_2.style.animationName="appear"
        this.container_2.style.zIndex="0"
    }
}