export default class RemoveProject
{
    constructor(project_header, project_main, container_2)
    {
        this.project_header = project_header 
        this.project_main = project_main 
        this.container_2 = container_2
        
        this.removeHeader()
        this.removeMain()
        this.removeContainer()
    }

    removeHeader()
    {
        this.project_header.style.transform="translateY(-100%)"
        this.project_header.style.transitionProperty="transform"
    }
    removeMain()
    {
        this.project_main.style.transform="translateY(100%)"
        this.project_main.style.transitionProperty="transform"
    }
    removeContainer()
    {
        this.container_2.style.animationName="disappear"
        this.container_2.style.zIndex="-10"
    }
}