export default class AddProjectDescription
{
    constructor(full_project_container, full_project_container_2, full_project_container_3, full_project_return)
    {
        this.full_project_container = full_project_container 
        this.full_project_container_2 = full_project_container_2 
        this.full_project_container_3 = full_project_container_3 
        this.full_project_return = full_project_return 
        
        this.addContainer()
        this.addButton()
    }

    addButton()
    {
        this.full_project_return.style.display="flex"
    }
    addContainer()
    {
        this.full_project_container.style.animationName="appear"
        this.full_project_container.style.display="block"
        this.full_project_container_2.style.animationName="appear"
        this.full_project_container_2.style.display="block"
        this.full_project_container_3.style.animationName="appear"
        this.full_project_container_3.style.display="block"
    }
}