export default class RemoveProjectDescription
{
    constructor(full_project_container, full_project_container2, full_project_container3,full_project_return, full_project_return_2)
    {
        this.full_project_container = full_project_container 
        this.full_project_container2 = full_project_container2 
        this.full_project_container3 = full_project_container3 
        this.full_project_return = full_project_return 
        this.full_project_return_2 = full_project_return_2
        
        this.addContainer()
        this.addButton()
    }

    addButton()
    {
        this.full_project_return.style.display="none"
        this.full_project_return_2.style.display="none"
    }
    addContainer()
    {
        this.full_project_container.style.animationName="disappear"
        this.full_project_container.style.display="none"
        this.full_project_container2.style.animationName="disappear"
        this.full_project_container2.style.display="none"
        this.full_project_container3.style.animationName="disappear"
        this.full_project_container3.style.display="none"
    }
}