export default class AddHomePage
{
    constructor(home_header, home_main, home_footer, container_1)
    {
        this.home_header = home_header 
        this.home_main = home_main 
        this.home_footer = home_footer 
        this.container_1 = container_1 
        
        this.addContainer()
        this.addHeader()
        this.addMain()
        this.addFooter()
    }

    addHeader()
    {
        this.home_header.style.transform="translateY(0%)"
        this.home_header.style.transitionProperty="transform"
    }
    addMain()
    {
        this.home_main.style.opacity="1"
        this.home_main.style.transitionProperty="opacity"
    }
    addFooter()
    {
        this.home_footer.style.transform="translateY(0%)"
        this.home_footer.style.transitionProperty="transform"
    }
    addContainer()
    {
        this.container_1.style.animationName="appear"
        this.container_1.style.zIndex="0"
    }
}