export default class AddContactPage
{
    constructor(top_left, top_right, bottom_left, bottom_right, container_3)
    {
        this.top_left = top_left 
        this.top_right = top_right 
        this.bottom_left = bottom_left
        this.bottom_right = bottom_right
        this.container_3 = container_3
        
        this.removeTopLeft()
        this.removeTopRight()
        this.removeBottomLeft()
        this.removeBottomRight()
        this.removeContainer()
    }

    removeTopLeft()
    {
        this.top_left.style.transform="translateY(0%)"
        this.top_left.style.transitionProperty="transform"
    }
    removeTopRight()
    {
        this.top_right.style.transform="translateX(0%)"
        this.top_right.style.transitionProperty="transform"
    }
    removeBottomLeft()
    {
        this.bottom_left.style.transform="translateX(0%)"
        this.bottom_left.style.transitionProperty="transform"
    }
    removeBottomRight()
    {
        this.bottom_right.style.transform="translateY(0%)"
        this.bottom_right.style.transitionProperty="transform"
    }
    removeContainer()
    {
        this.container_3.style.animationName="appear"
        this.container_3.style.zIndex="0"
    }
}