export default class RemoveHome
{
    constructor(home_header, home_main, home_footer, container_1)
    {
        this.home_header = home_header 
        this.home_main = home_main 
        this.home_footer = home_footer 
        this.container_1 = container_1 
        
        this.removeHeader()
        this.removeMain()
        this.removeFooter()
        this.removeContainer()
    }

    removeHeader()
    {
        this.home_header.style.transform="translateX(-100%)"
        this.home_header.style.transitionProperty="transform"
    }
    removeMain()
    {
        this.home_main.style.opacity="0"
        this.home_main.style.transitionProperty="opacity"
    }
    removeFooter()
    {
        this.home_footer.style.transform="translateY(100%)"
        this.home_footer.style.transitionProperty="transform"
    }
    removeContainer()
    {
        this.container_1.style.animationName="disappear"
        this.container_1.style.zIndex="-10"
    }
}